	module.exports = {"data":{"markdownRemark":{"html":"<p>例年に増して残暑厳しかった幻想郷にも紅葉の季節がやってきた。そして予想外に涼しい風の抜けるこの神社では、今宵も人と妖怪が集まってきた。もう神無月にもなろうかと言うのに、ここでは満月の夜の度にお月見宴会が開かれる。 「やっぱりこの満月が一番ね」 「あー？私にはさっぱり区別つかないぜ」 「うぅ、お酒まだぁ～？」 「咲夜が壊れるなんて……あなた、どれだけ飲ませたの？」 なんてことはない、いつもどおりの宴会である。ただ、この宴の参加者の中では一番酒に強い霊夢はちょっとした違和感に気付いていた。今宵は、いつになく酒酔いの回りが早いことに……幻想郷では上位5本の指に入るほどの酒豪である霊夢も、覚えていることはそこまでだった。 次に霊夢が目が覚めた時には、既に翌日の夜になっていた。……否、行灯の蝋燭は6時間ほどしか持たないはずなのに火がついていたのでまだ夜が明けてないだけと言うことだろう。ただ、それにしては長すぎる夜である。もう鶏が鳴き始めているのに天空を満月が支配し、明るくなる兆しを見せない。しばらくして皆もそろって酒が抜けて目が覚めるが、そのときに皆そろって異変に気がついた。そう、また夜が止まっているのである。 「満月は取り戻せたのに、一体なんなのかしら」 「おかしいぜ、ちょっと調べてみたほうがいいのかもな」 「じゃあその辺は任せて私たちは宴会の続きをやりましょみょん」 不審に思った数名は宴会を残りの者に任せ、原因探しに飛び立った。 それから数分後、残って宴会を楽しんでいた一人の妖怪が、遠くに覚えのある妖力を感じ、密かに青ざめた。だが、周りの者は彼女が青ざめていることに誰も気付かなかったし、気付いたとしても悪酒の所為だと思われていたようだ。 そう、彼女は常にポーカーフェイスなのだ。楽しいときも、退屈なときも。</p>","frontmatter":{"cover":null,"date":"2004-10-05T00:01:57+09:00","strDate":"2004/10/4","title":"ストーリー"}}},"pathContext":{"path":"/ja/blog/2004/10/05-ストーリー-2/","langKey":"ja"}}

